.nav-check{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}
.nav-check a{
    text-align: center;
    height:110px;
    background-color: #fff;
    width: 42%;
    margin:10px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-shadow: 0 2px 17px 0 rgba(199,199,199,0.50);
            box-shadow: 0 2px 17px 0 rgba(199,199,199,0.50);
}
.nav-check label{
    padding-top:4px;
}
.desc{
    font-size:12px;
    text-align: center;
    color: #4C4C4C;
    /*background:#fefdfb;*/
    margin-bottom:10px;
    -webkit-box-shadow: 0 2px 4px 0 rgba(199,199,199,0.20);
            box-shadow: 0 2px 4px 0 rgba(199,199,199,0.20);
}
.desc p{
    margin-top:-30px;
    height:30px;
    line-height: -30px;
}


